<template>
    <div class="forgetPwd">
        <hheader :header="hheader">
            <router-link to="/login" style="color:white;" slot="hright">
                登录
            </router-link>
        </hheader>
        <div class="container has-header">
            <div v-show="step==1">
                <br/>
                <van-field v-model="email" type="email" placeholder="请输入您的邮箱" />
                <van-field v-model="code" placeholder="请输入您的邮箱验证码" />
                <br/>
                <van-button type="primary" size="large" @click.native="next">下一步</van-button>
            </div>
            <div v-show="step==2">
                <br/>
                <van-field v-model="newPwd" placeholder="请输入您的新密码" />
                <van-field v-model="confirmPwd" placeholder="请再次输入您的新密码" />
                <br/>
                <van-button type="primary" size="large" @click.native="applay">提交</van-button>
            </div>
        </div>
    </div>
</template>

<script>
import { Toast } from "vant";
export default {
    data() {
        return {
            step: 1,
            hheader: {
                title: "找回密码"
            },
            email: "",
            code: "",
            newPwd: "",
            confirmPwd: "",
            showBtn: 1,
            checked: false
        };
    },
    methods: {
        //下一步
        next() {
            let self = this;
            var reg = new RegExp(
                "^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$"
            );
            if (!reg.test(self.email)) {
                Toast("邮箱不正确");
                return false;
            }
            if (!self.code) {
                Toast("邮箱验证码不能为空");
                return false;
            }
            self.step = 2;
        },
        //提交申请
        applay() {
            let self = this;
            if (!self.newPwd || !self.confirmPwd) {
                Toast("请输入密码");
                return false;
            }
            if (self.newPwd != self.confirmPwd) {
                Toast("两次密码输入不相同");
                return false;
            }
            let data = {
                newPwd: self.newPwd,
                confirmPwd: self.confirmPwd
            };
            self.$.Post("", data, re => {
                console.log(re);
            });
        }
    }
};
</script>

<style lang="scss" rel="stylesheet/scss">
@import "../assets/hcsy.scss";
.forgetPwd {
    .van-button {
        display: block;
        width: 92%;
        margin: 0 auto;
        border-radius: rem(70);
        background-color: $color;
    }
}
</style>



